body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Total {
  width: 100%;
}
.Title {
  display: flex;
  text-align: left;
  background: rgb(255, 255, 255);
  color: rgb(49, 49, 49);
  font-size: 36px;
  padding: 2rem;
  font-weight: 900;
}

.PcImg {
  flex: 1 1;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(/Self-Introduction/static/media/pcImage.bc85e381.jpg);
  width: 100%;
  height: 720px;
}
.Body {
  flex-direction: column;
  align-items: center;
  background: #492b56;
  min-height: 80em;
}

.Bodyed {
  height: 100vh;
  background: #2c1c3e;
  align-self: center;
  align-items: center;
  justify-content: center;
  box-shadow: 0px -5px 0px 0px rgba(78, 0, 167, 0.274);
}

.BG {
  display: flex;
  align-self: center;
  align-items: center;
  justify-content: center;
}

.Software{
  text-align: center;
  padding-top: 150px;
  margin-bottom: 10px;
  color: #292929;
  font-size: 50px; 
  font-weight: 800;
}

.MyName{
  margin-top: 10px; 
  margin-bottom: 80px;
  color: #292929;
  font-size: 50px;
  font-weight: 800;
  text-align: center;
}
.MySelf {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-Top: 250px;
  align-self: center;
  height: 500px;
  width: 500px;
  border-radius: 250px;
  background-color: #ffffff;
  flex-direction: column;
  box-shadow: 0px 0px 0px 10px #2c1c3e;
 
}

.PhoneNumber {
  color: rgb(255, 255, 255);
  font-size: 18px;
  font-weight: 500;
  margin-left: 20px;
}

.Input {
  color: white;
  font-size: 15px;
  margin-bottom: 20px;
  margin-left: 25px;
}

.SNS {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 500px;
  height: 150px;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  border-bottom-left-radius: 150px;
  border-bottom-right-radius: 150px;
  align-self: center;
  background: #2c1c3e;
}

.Welcome {
  padding-top : 40px;
  font-size: 55px;
  text-align: center;
  font-weight: 700;
  color: white;

}

.Contents {
  padding-top : 200px;
  font-size: 17px;
  text-align: left;
  font-weight: 200;
  color: white;
  padding-left: 250px;
  padding-right: 250px;
}

@media screen and (min-width: 320px) and (max-width: 667px) {
  .Title {
    justify-content: center;
  }
  .Contents {
    font-size: 14px;
    padding-left: 3%;
    padding-right: 3%;
  }
  .PhoneNumber{
    font-size: 12px;
    font-weight: 500;
    margin-left: 10px;
  }
  .Input{
    font-size: 13px;
    margin-bottom: 20px;
    margin-left: 15px;
  }
  .MySelf {
    height: 300px;
    width: 300px;
    border-radius: 150px;
    margin-Top: 70%;
  }
  .Software{
    text-align: center;
    padding-top: 80%;
    margin-bottom: 5%;
    font-size: 30px; 
    font-weight: 800;
  }
  .MyName{
    margin-top: 5%; 
    margin-bottom: 40%;
    font-size: 30px;
    font-weight: 800;
  }
  .SNS {
    width: 300px;
    height: 100px;
    border-bottom-left-radius: 150px;
    border-bottom-right-radius: 150px;
  }
  .Welcome{
    padding-top : 7%;
    font-size: 35px;
  }
  .Contents {
    padding-top : 35%;
  }
}
